<template>
  <b-card
    no-body
    class="mb-0"
  >
    <b-modal
      v-model="modalShow"
      header-bg-variant="primary"
      no-close-on-esc
      no-close-on-backdrop
      hide-header-close
      hide-footer
      centered
    >
      <template
        #modal-header
        class="text-center"
      >
        <h4 class="text-white">
          Registro de asistencia
        </h4>
      </template>
      <div class="text-center my-4">
        <b-button
          v-if="!spinnerShow"
          variant="danger"
          @click="onIniciarAsistencia"
        >
          Iniciar
        </b-button>
        <b-spinner
          v-else
          style="width: 5rem; height: 5rem;"
          variant="primary"
          label="Text Centered"
        />
      </div>
    </b-modal>

    <div class="m-2">
      <b-row>
        <b-col md="4">
          <b-form @submit.prevent="onSubmit">
            <b-form-group
              label="Comida"
              label-class="text-uppercase font-weight-bold"
            >
              <b-form-select
                v-model="comida"
                class="text-uppercase"
                :options="options"
                @input="actualizarTabla"
              />
            </b-form-group>
            <b-form-group
              label-class="text-uppercase font-weight-bold"
              label="Ingresar DNI"
            >
              <b-form-input
                v-model="numero_documento"
                placeholder="Ingresar DNI"
              />
            </b-form-group>
            <b-button
              type="submit"
              variant="primary"
              block
            >
              Registrar
            </b-button>
          </b-form>
        </b-col>
        <b-col>
          <b-card
            :header="dataResponse.mensaje"
            :header-bg-variant="cardColor"
            header-text-variant="light"
            header-class="text-uppercase font-weight-bold"
            :border-variant="cardColor"
          >
            <b-card-body>
              <!-- DNI -->
              <b-row>
                <b-col cols="5">
                  DNI:
                </b-col>
                <b-col cols="7">
                  {{ dataResponse.asistencia.persona.numero_documento }}
                </b-col>
              </b-row>

              <!-- Nombre y apellidos -->
              <b-row>
                <b-col cols="5">
                  Nombres y apellidos:
                </b-col>
                <b-col cols="7">
                  {{ dataResponse.asistencia.persona.nombre_completo }}
                </b-col>
              </b-row>

              <!-- Fecha y hora de registro -->
              <b-row>
                <b-col cols="5">
                  Fecha de registro:
                </b-col>
                <b-col cols="7">
                  {{ dataResponse.fecha_registro }}
                </b-col>
              </b-row>
              <!-- Fecha y hora de registro -->
              <b-row>
                <b-col cols="5">
                  Comida:
                </b-col>
                <b-col
                  cols="7"
                  class="text-uppercase"
                >
                  {{ dataResponse.comida }}
                </b-col>
              </b-row>

            </b-card-body>

            <!-- {{ dataResponse.usuario.name }} <br>
            {{ dataResponse.fecha_registro }} <br>
            {{ dataResponse.mensaje }} -->
          </b-card>
        </b-col>
      </b-row>
    </div>

    <b-table
      id="my-table"
      ref="refTablaAsistencia"
      class="position-relative"
      show-empty
      head-variant="light"
      hover
      small
      responsive
      :busy.sync="isBusy"
      :items="myProvider"
      :fields="fields"
      :current-page="currentPage"
      :empty-text="'No hay registros para mostrar'"
      :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
      :per-page="perPage"
      :filter="filter"
      :filter-included-fields="filterOn"
      :sort-by="sortBy"
      :sort-desc="sortDesc"
    >
      <template #table-busy>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle" />
          <strong>Cargando...</strong>
        </div>
      </template>

    </b-table>

    <div class="mx-2 mb-2">
      <b-row>
        <b-col
          cols="12"
          sm="6"
          class="d-flex align-items-center justify-content-center justify-content-sm-start"
        >
          <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>

        </b-col>
        <!-- Pagination -->
        <b-col
          cols="12"
          sm="6"
          class="d-flex align-items-center justify-content-center justify-content-sm-end"
        >

          <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            first-number
            last-number
            class="mb-0 mt-1 mt-sm-0"
            prev-class="prev-item"
            next-class="next-item"
          >
            <template #prev-text>
              <feather-icon
                icon="ChevronLeftIcon"
                size="18"
              />
            </template>
            <template #next-text>
              <feather-icon
                icon="ChevronRightIcon"
                size="18"
              />
            </template>
          </b-pagination>

        </b-col>
      </b-row>
    </div>
  </b-card>
</template>

<script>
import {
  BForm, BFormGroup, BFormInput, BButton, BCard, BTable, BSpinner, BPagination, BRow, BCol, BFormSelect, BCardBody,
} from 'bootstrap-vue'
import { onUnmounted } from '@vue/composition-api'
import Ripple from 'vue-ripple-directive'
import store from '@/store'
import asistenciaBecariosStoreModule from './asistenciaBecariosStoreModule'

export default {
  components: {
    BForm, BFormGroup, BFormInput, BButton, BCard, BTable, BSpinner, BPagination, BRow, BCol, BFormSelect, BCardBody,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      cardColor: '',
      modalShow: true,
      spinnerShow: false,
      options: ['desayuno', 'almuerzo', 'cena', 'otros'],
      selected: null,
      numero_documento: null,
      comida: null,
      dataResponse: {
        mensaje: null,
        fecha_registro: null,
        comida: null,
        usuario: {
          name: '',
        },
        asistencia: {
          persona: {
            nombre_completo: null,
          },
        },
      },
      dataResponseDefault: {
        mensaje: null,
        fecha_registro: null,
        comida: null,
        usuario: {
          name: '',
        },
        asistencia: {
          persona: {
            nombre_completo: null,
          },
        },
      },
      isBusy: false,
      mostrar: false,
      items: [],
      fields: [
        { key: 'persona.nombre_completo', label: 'comensal', sortable: true },
        {
          key: 'comida', label: 'comida', sortable: true, class: 'text-uppercase',
        },
        { key: 'fecha_registro', label: 'fecha_registro', sortable: true },
      ],
      from: 1,
      to: 1,
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      pageOptions: [5, 10, 15, 100],
      sortBy: 'id',
      sortDesc: false,
      sortDirection: 'desc',
      filter: '',
      filterOn: [],
    }
  },
  methods: {
    actualizarTabla() {
      this.$refs.refTablaAsistencia.refresh()
    },
    success(message) {
      this.$swal({
        title: `${message}`,
        icon: 'success',
        customClass: {
          confirmButton: 'btn btn-primary',
        },
        buttonsStyling: false,
      })
    },
    onIniciarAsistencia() {
      this.spinnerShow = true
      store.dispatch('asistencia/crearRegistroAsistencia')
        .then(response => {
          this.spinnerShow = false
          this.modalShow = false
          this.success(response.data)
          // console.log(response)
        })
    },
    onSubmit() {
      const formData = {
        numero_documento: this.numero_documento,
        comida: [this.comida],
      }

      store.dispatch('asistencia/addBecarioAsistencias', formData)
        .then(response => {
          this.cardColor = 'success'
          // this.dataResponse = response.data
          this.dataResponse = response.data.data
          // eslint-disable-next-line prefer-destructuring
          this.dataResponse.comida = response.data.data.comida[0]
          this.actualizarTabla()
        })
        .catch(error => {
          if (error.response.data.code === 422) {
            this.cardColor = 'warning'
            this.dataResponse = error.response.data.error.data
            // eslint-disable-next-line prefer-destructuring
            this.dataResponse.comida = error.response.data.error.data.comida[0]
          }
          if (error.response.data.code === 404) {
            this.cardColor = 'danger'
            this.dataResponse = Object.assign(this.dataResponseDefault, {})
            this.dataResponse.mensaje = error.response.data.error
          }
        })
    },
    async myProvider(ctx) {
      // eslint-disable-next-line no-param-reassign
      ctx.fecha_registro = `${new Date().getFullYear()}-${new Date().getMonth()}-${new Date().getDate()}`
      // eslint-disable-next-line no-param-reassign
      ctx.comida = this.comida
      // eslint-disable-next-line no-param-reassign
      ctx.asistencia = 1

      const promise = store.dispatch('asistencia/fetchBecarioAsistencias', { queryParams: ctx })
      return promise.then(response => {
        const {
          data, total, from, to,
        } = response.data

        const items = data
        this.totalRows = total || 0
        this.from = from || 0
        this.to = to || 0
        return items || []
      })
    },
  },
  setup() {
    const ASISTENCIA_APP_STORE_MODULE_NAME = 'asistencia'

    if (!store.hasModule(ASISTENCIA_APP_STORE_MODULE_NAME)) store.registerModule(ASISTENCIA_APP_STORE_MODULE_NAME, asistenciaBecariosStoreModule)

    onUnmounted(() => {
      if (store.hasModule(ASISTENCIA_APP_STORE_MODULE_NAME)) store.unregisterModule(ASISTENCIA_APP_STORE_MODULE_NAME)
    })

    return {

    }
  },
}
</script>

<style lang="scss" scoped>

</style>
